/* Custom Navbar Styles */
.navbar {
    padding: 1%; /* Adjust padding for the navbar */
    background-color: #343a40; /* Dark background for the navbar */
  }
  
  .nav-item {
    color: #fff; /* White text color for the navbar items */
  }
  
  .navbar-brand {
    color: #fff; /* White color for the brand name in the navbar */
  }
  
  .nav-link {
    color: #fff; /* White color for the navigation links */
    padding: 0.5rem 1rem; /* Adjust padding for the links */
    text-transform: uppercase; /* Make the text uppercase */
  }
  
  .nav-link:hover {
    color: #007bff; /* Change link color on hover */
  }
  
  .navbar-toggler {
    border-color: #007bff; /* Color for the navbar toggler button border */
  }
  
  .navbar-toggler-icon {
    background-color: #007bff; /* Color for the toggler icon */
  }
  
  .collapse.navbar-collapse {
    justify-content: flex-end; /* Align the links to the right */
  }
  
  /* Footer Styles */
  .footer {
    background-color: #343a40; /* Dark background for the footer */
    color: #fff; /* White text color for the footer */

    margin-bottom: 0%;
  }
  
