/* src/pages/Aboutus_Components/Card.css */
.card {
  display: flex;
  margin-bottom: 1rem; /* Space between cards */
  border: 3px solid black; /* Optional: border around card */
  border-radius: 8px; /* Optional: rounded corners */
  overflow: hidden; /* Ensure the image does not overflow */
  padding: 2%;
  
  color:black;
  font-size: 20px;

}

.card-img {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ensures image covers the area without distortion */
}

.card-body {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}

.card-text {
  text-align: justify; /* Center text inside the card-body */
  margin-top: 50px;
 
}

