/* Increase the width and height of the contact form */
.contact-form {
  background-color: #090506; /* Ensure background color is as desired */
  width: 100%; /* Adjust width as needed */
  max-width: 600px; /* Increase max-width for larger screens */
  height: auto; /* Ensure height adjusts based on content */
  margin: 0 auto; /* Center the form horizontally */
  padding: 20px;
  border-radius: 10px;
  color: white;
}

/* Ensure the form inputs and button are styled consistently */
form input, form textarea {
  border: 1px solid #ccc; /* Add border for better visibility */
  padding: 12px;
  margin: 10px 0;
  outline: none;
  border-radius: 10px;
}

form button {
  padding: 15px;
  background-color: #fcc200;
  font-size: 18px;
  border: none; /* Remove border if not needed */
  cursor: pointer;
  width: 100%; /* Make the button span full width */
  margin: 20px 0 0;
  border-radius: 30px;
}
